@tailwind base;
@tailwind components;
@tailwind utilities;

/* Global */
body {
  @apply bg-gray-50 text-gray-800 font-sans scroll-smooth;
}

/* Scrollbar */
::-webkit-scrollbar { width: 8px; }
::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #2563eb, #3b82f6);
  border-radius: 10px;
}
::-webkit-scrollbar-track { background: #e5e7eb; }

/* Section Headings */
section h2 {
  @apply text-3xl md:text-4xl font-bold text-blue-800 mb-8 tracking-wide relative inline-block;
}
section h2::after {
  content: "";
  display: block;
  width: 5rem;
  height: 0.25rem;
  background: #2563eb;
  border-radius: 0.375rem;
  margin: 0.5rem auto 0;
}

/* Buttons */
.btn-primary {
  @apply px-6 py-3 bg-blue-700 text-white font-medium rounded-lg shadow hover:bg-blue-800 transition duration-300;
}
.btn-outline {
  @apply px-6 py-3 border border-blue-700 text-blue-700 font-medium rounded-lg hover:bg-blue-100 transition duration-300;
}

/* Fade animation */
.fade-in { animation: fadeIn 1s ease-in-out both; }
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Navbar glassmorphism */
.navbar-blur {
  background: rgba(255, 255, 255, 0.65);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
}

/* Card hover */
.card-hover {
  transition: all 0.3s ease;
}
.card-hover:hover {
  transform: translateY(-6px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
}

/* Footer */
footer {
  background: linear-gradient(90deg, #1e3a8a, #2563eb);
}
